import {attr, controller, target} from '@github/catalyst'
import AudioPlayer from './audio-player'

/**
 * This code is maintained in `github/media-players` which is also a playground for experimenting.
 * Please do your best so the uptades made are in sync.
 **/
@controller
// eslint-disable-next-line unused-imports/no-unused-vars
class VideoPlayer extends AudioPlayer {
  controlsHideTimer: number | null = null
  controlsHideTimerDuration = 5000

  @attr controlsShown = true
  @attr isFullscreen = false
  @attr captionsVisible = true
  @attr posterHidden = false
  @attr overlayHidden = false

  @target poster: HTMLElement
  @target overlay: HTMLElement
  @target controls: HTMLElement
  @target fullscreenToggleButton: HTMLElement
  @target fullscreenTooltip: HTMLElement
  @target track: HTMLTrackElement
  @target captions: HTMLElement
  @target captionsToggleButton: HTMLElement
  @target captionsTooltip: HTMLElement
  @target controlsTop: HTMLElement

  // Since we are extending another Catalyst element, we need to use a regular constructor and a custom event
  // instead of onConnect cause onConnect here would override the one set up in the Audio Player
  constructor() {
    super()

    this.addEventListener('media-player-connected', () => {
      if (this.controls) {
        this.media.controls = false
        this.controls.hidden = false
      }

      if (this.overlay) this.overlay.hidden = false
      if (this.poster) this.poster.hidden = false
      if (this.controlsTop) this.controlsTop.hidden = false

      if (this.browserIsIOS) this.classList.add('media-player-browser-is-ios')

      this.media.addEventListener('play', () => {
        this.setVideoPlayState(false)
      })

      this.media.addEventListener('pause', () => {
        this.setVideoPlayState(true)
      })

      this.initCaptions()
      this.initFullscreen()
    })

    this.addEventListener('media-player-ready', () => {
      this.setupScrubberVideoEvents()
      this.initVideoOverlay()
      this.initVideoControlsHide()
    })
  }

  setupScrubberVideoEvents() {
    if (!this.scrubber) return

    this.scrubber.addEventListener('change', () => {
      this.posterHidden = true
    })

    this.scrubber.addEventListener('input', () => {
      this.posterHidden = true
    })
  }

  initVideoOverlay() {
    if (!this.media) return

    if (this.overlay) {
      if (!this.browserIsMobile) {
        this.overlay.addEventListener('click', this.togglePlay.bind(this))
      } else {
        if (this.media && this.media.paused) this.overlay.addEventListener('click', this.play.bind(this))
      }
    }
  }

  showControls() {
    this.controlsShown = true
    if (this.controlsHideTimer) clearTimeout(this.controlsHideTimer)
    if (this.media && this.media.paused) return
    this.controlsHideTimer = window.setTimeout(() => {
      this.controlsShown = false
    }, this.controlsHideTimerDuration)
  }

  initVideoControlsHide() {
    if (!this.media) return

    const controlsShowEvent = !this.browserIsMobile ? 'mousemove' : 'touchstart'
    this.addEventListener(controlsShowEvent, () => {
      this.showControls()
    })

    this.addEventListener('media-player-keyboard-interaction', () => {
      this.showControls()
    })
  }

  setVideoPlayState(isPaused: boolean) {
    if (isPaused) {
      this.overlayHidden = false
      this.controlsShown = true
      if (this.controlsHideTimer) clearTimeout(this.controlsHideTimer)
      return
    }

    this.overlayHidden = true
    this.posterHidden = true
    this.controlsShown = false
  }

  // Skip forward override to hide the poster
  override skipForward() {
    this.skip(false)
    this.posterHidden = true
    this.interactionEvent('skip-forward')
  }

  // CAPTIONS
  initCaptions() {
    if (!this.track) {
      if (this.captions) this.captions.remove()
      return
    }

    if (!this.captions) return
    if (this.captionsToggleButton) this.captionsToggleButton.hidden = false

    const savedCaptionsState = <boolean>this.retrieveValue('captions')
    if (savedCaptionsState !== null) {
      this.captionsVisible = savedCaptionsState
      this.setCaptionsState(savedCaptionsState)
    }

    this.track.addEventListener('cuechange', event => {
      if (!this.captions || !event.target) return
      const targetTrack = event.target as HTMLTrackElement
      if (!targetTrack || !targetTrack.track) return
      const cues = targetTrack.track.activeCues
      if (!cues || !cues.length) {
        this.captions.hidden = true
        return
      }

      // hides captions on Moz FF
      if (!this.browserIsIOS) targetTrack.track.mode = 'hidden'

      this.captions.hidden = false
      // @ts-expect-error types don't account for this usage
      this.captions.textContent = cues[0].text
    })
  }

  override toggleCaptions() {
    if (!this.captionsToggleButton) return
    this.setCaptionsState(!this.captionsVisible)
    this.interactionEvent(this.captionsVisible ? 'captions-on' : 'captions-off')
  }

  setCaptionsState(visible: boolean) {
    this.captionsVisible = visible
    if (!this.captionsToggleButton) return
    this.storeValue('captions', visible)

    const label = visible
      ? this.captionsToggleButton.getAttribute('data-label-disable')
      : this.captionsToggleButton.getAttribute('data-label-enable')
    if (label) {
      this.captionsToggleButton.setAttribute('aria-label', label)
      if (this.showTooltips && this.captionsTooltip) this.captionsTooltip.textContent = label
    }
  }

  // FULLSCREEN
  initFullscreen() {
    if (this.fullscreenToggleButton && !this.supportsFullscreen()) {
      this.fullscreenToggleButton.style.display = 'none'
      return
    }

    document.addEventListener('fullscreenchange', () => {
      // @ts-expect-error types don't account for browser specific methods
      this.setFullscreenState(!!(document.fullscreenElement || document.webkitFullscreenElement))
    })

    document.addEventListener('webkitfullscreenchange', () => {
      // @ts-expect-error types don't account for browser specific methods
      if (!document.webkitIsFullScreen) this.setFullscreenState(false)
    })

    if (this.browserIsIOS) {
      this.media.addEventListener('webkitendfullscreen', () => {
        this.setFullscreenState(false)
      })
    }
  }

  enterFullscreen() {
    this.setFullscreenState(true)

    if (this.requestFullscreen) {
      this.requestFullscreen()
      return
    }

    if (this.browserIsIOS && this.media) {
      // @ts-expect-error types don't account for browser specific methods
      this.media.webkitEnterFullscreen()
      return
    }

    // @ts-expect-error types don't account for browser specific methods
    if (this.webkitRequestFullScreen && this.media && this.media.parentElement) {
      // @ts-expect-error types don't account for browser specific methods
      this.media.parentElement.webkitRequestFullScreen()
      return
    }
  }

  exitFullscreen() {
    this.setFullscreenState(false)

    if (document.exitFullscreen) {
      document.exitFullscreen()
      return
    }

    if (this.browserIsIOS && this.media) {
      // @ts-expect-error types don't account for browser specific methods
      this.media.webkitExitFullscreen()
      return
    }

    // @ts-expect-error types don't account for browser specific methods
    if (document.webkitCancelFullScreen) {
      // @ts-expect-error types don't account for browser specific methods
      document.webkitCancelFullScreen()
      return
    }
  }

  setFullscreenState(isFullscreen: boolean) {
    this.isFullscreen = isFullscreen
    this.interactionEvent(isFullscreen ? 'fullscreen-enter' : 'fullscreen-exit')
    if (!this.fullscreenToggleButton) return

    const label = isFullscreen
      ? this.fullscreenToggleButton.getAttribute('data-label-exit')
      : this.fullscreenToggleButton.getAttribute('data-label-enter')
    if (label) {
      this.fullscreenToggleButton.setAttribute('aria-label', label)
      if (this.showTooltips && this.fullscreenTooltip) this.fullscreenTooltip.textContent = label
    }
  }

  override toggleFullscreen() {
    if (this.fullscreenToggleButton && !this.isFullscreen) {
      this.enterFullscreen()
      return
    }

    this.exitFullscreen()
  }
}
